.search-form {
  margin-top: 40px;
  margin-bottom: 30px;
  padding: 30px;
  width: 70%;

  background-color: rgba(255, 255, 255, 0.51);
  border-radius: 10px;
  border: 1.3px rgba(80, 149, 35, 0.52) solid;

  z-index: 10;

  &__form {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  &__button {
    padding: 10px;
  }
}
